.nav {
  position: relative;
}

.nav--closed .nav__toggle {
  position: absolute;
  top: -61px;
  right: 20px;
  z-index: 2;
  width: 45px;
  height: 24px;
}
.nav--closed .nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 45px;
  height: 2.5px;
  background-color: #559127;
  box-shadow: 0 10px 0 #559127, 0 20px 0 #559127;
}
@media (min-width: 360px) {
  .nav--closed .nav__toggle {
    right: 40px;
  }
}
@media (min-width: 660px) {
  .nav--closed .nav__toggle {
    top: -66px;
  }
}
@media (min-width: 1200px) {
  .nav--closed .nav__toggle {
    display: none;
  }
}

.nav--opened .nav__toggle {
  position: absolute;
  top: -61px;
  right: 20px;
  width: 45px;
  height: 24px;
}
.nav--opened .nav__toggle::before, .nav--opened .nav__toggle::after {
  content: "";
  position: absolute;
  top: 10px;
  left: 6px;
  width: 31px;
  height: 2.5px;
  background-color: #559127;
  border-radius: 0;
  box-shadow: none;
}
.nav--opened .nav__toggle::before {
  transform: rotate(45deg);
}
.nav--opened .nav__toggle::after {
  transform: rotate(-45deg);
}
@media (min-width: 360px) {
  .nav--opened .nav__toggle {
    right: 40px;
  }
}
@media (min-width: 660px) {
  .nav--opened .nav__toggle {
    top: -66px;
  }
}
@media (min-width: 1200px) {
  .nav--opened .nav__toggle {
    display: none;
  }
}

